/*-----------------------------------*\
  #NAVBAR
\*-----------------------------------*/

.navbar {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background: hsla(240, 1%, 17%, 0.75);
    backdrop-filter: blur(10px);
    border-radius: 12px 12px 0 0;
    box-shadow: var(--shadow-2);
    z-index: 5;
}

.navbar-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 0 10px;
}

.navbar-link {
    color: var(--light-gray);
    font-size: var(--fs-8);
    padding: 20px 7px;
    transition: color var(--transition-1);
}

.navbar-link:hover,
.navbar-link:focus {
    color: var(--light-gray-70);
}

.navbar-link.active {
    color: var(--orange-yellow-crayola);
}


@media (min-width: 200px) {
    
  /**
   * #NAVBAR
   */

  .navbar { 
    border-radius: 20px 20px 0 0;
    border-top: 3px solid var(--orange-yellow-crayola);
  }

  .navbar-link { 
    font-weight: var(--fw-600); 
    font-size: 18px;
  }
}



@media (min-width: 1024px) {
  .navbar {
    position: absolute;
    bottom: auto;
    top: 0;
    left: auto;
    right: 0;
    border-left: 3px solid var(--orange-yellow-crayola);
    border-bottom: 3px solid var(--orange-yellow-crayola);
    border-top: 0px;
    width: max-content;
    border-radius: 0 20px;
    padding: 0 20px;
    box-shadow: none;
  }

  .navbar-list {
    gap: 30px;
    padding: 0 20px;
  }

  .navbar-link { 
    font-weight: var(--fw-600); 
    font-size: 15px;
  }
}